import BScroll from "better-scroll";
export default {
  data() {
    return {
      left: ["a", "b", "c", "d", "e", "f"],
      right: [
        {
          name: "a",
          content: ["1", "2", "3", "4", "5"]
        },
        {
          name: "b",
          content: ["1", "2", "3", "4", "5"]
        },
        {
          name: "c",
          content: ["1", "2", "3", "4", "5"]
        },
        {
          name: "d",
          content: ["1", "2", "3", "4", "5"]
        },
        {
          name: "e",
          content: ["1", "2", "3", "4", "5"]
        },
        {
          name: "f",
          content: ["1", "2", "3", "4", "5"]
        }
      ],
      listHeight: [],
      scrollY: 0, //y
      clickEvent: false
    };
  },
  methods: {
    _initScroll() {
      //better-scrolltouchStart,touchendpreventDefault
      //click,
      //pc22$event
      this.lefts = new BScroll(this.$refs.left, {
        click: true
      });
      this.rights = new BScroll(this.$refs.right, {
        probeType: 3 //
      });
      //rightspos.y
      this.rights.on("scroll", pos => {
        this.scrollY = Math.abs(Math.round(pos.y));
      });
    },
    _getHeight() {
      let rightItems = this.$refs.right.getElementsByClassName(
        "right-item-hook"
      );
      let height = 0;
      this.listHeight.push(height);
      for (let i = 0; i < rightItems.length; i++) {
        let item = rightItems[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    },
    selectItem(index, event) {
      this.clickEvent = true;
      //better-scrolleventevent_constructed
      //_constructedreturn
      if (!event._constructed) {
        return;
      } else {
        let rightItems = this.$refs.right.getElementsByClassName(
          "right-item-hook"
        );
        let el = rightItems[index];
        this.rights.scrollToElement(el, 300);
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this._initScroll();
      this._getHeight();
    });
  },
  computed: {
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        //height2heightheight2
        //>=heightthis.scrollY=0,height=0
        if (!height2 || (this.scrollY >= height && this.scrollY < height2)) {
          return i;
        }
        if (
          this.listHeight[this.listHeight.length - 1] -
            this.$refs.right.clientHeight <=
          this.scrollY
        ) {
          if (this.clickTrue) {
            return this.currentNum;
          } else {
            return this.listHeight.length - 1;
          }
        }
      }
      //this.listHeight0
      return 0;
    },
    optList() {
      //
      return [
        {
          level: 1,
          opt_id: 15,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 1281,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 16,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 13,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 14,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 1,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 4,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 743,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 18,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 1282,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 818,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 2478,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 2048,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 1451,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 1917,
          opt_name: "",
          parent_opt_id: 0
        },
        {
          level: 1,
          opt_id: 590,
          opt_name: "",
          parent_opt_id: 0
        }
      ];
    }
  }
};



